<template>
  <div>
    <el-container>
      <el-header style="padding: 0px;display:flex;justify-content:space-between;align-items: center">
        <div style="display: inline">
          <el-input
            placeholder="通过护工姓名搜索护工信息,记得回车哦."
            clearable
            @change="keywordsChange"
            style="width: 300px;margin: 0px;padding: 0px;"
            size="mini"
            :disabled="advanceSearchViewVisible"
            @keyup.enter.native="searchEmp"
            prefix-icon="el-icon-search"
            v-model="roleName">
          </el-input>
          <el-button type="primary" size="mini" style="margin-left: 5px" icon="el-icon-search" @click="searchEmp">搜索
          </el-button>
        </div>
        <div style="margin-left: 5px;margin-right: 20px;display: inline">
          <el-button type="primary" size="mini" icon="el-icon-plus"
                     @click="showAddEmpView">
            添加护工
          </el-button>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <div>
          <el-table
            :data="patientList"
            v-loading="tableLoading"
            border
            size="mini"
            style="width: 100%">
            <el-table-column
              prop="code"
              align="center"
              label="护工编号" >
            </el-table-column>
            <el-table-column
              prop="id"
              v-if="false"
              align="center"
             >
            </el-table-column>
            <el-table-column
              prop="name"
              align="center"
              label="姓名">
            </el-table-column>
            <el-table-column
              prop="sex"
              align="center"
              label="性别">
            </el-table-column>
            <el-table-column
              prop="idcard"
              align="center"
              label="身份证">
            </el-table-column>
            <el-table-column
              prop="jodtitle"
              align="center"
              label="职务">
            </el-table-column>
            <el-table-column
              prop="phone"
              align="center"
              label="联系方式">
            </el-table-column>
            <el-table-column
              prop="address"
              align="center"
              label="联系地址">
            </el-table-column>
            <el-table-column
              align="center"
              label="操作"
              width="300">
              <template slot-scope="scope">
                <el-button @click="showEditEmpView(scope.row)" style="padding: 3px 10px 3px 10px;margin: 2px" size="small">编辑
                </el-button>
                <el-button  @click="transaView(scope.row)"    style="padding: 3px 10px 3px 10px;margin: 2px" size="small">添加行为记录
                </el-button>
                <el-button  @click="arrearageView(scope.row)"    style="padding: 3px 10px 3px 10px;margin: 2px" size="small">查询行为记录
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
            <el-pagination
              background
              :page-size="10"
              :current-page="currentPage"
              @current-change="currentChange"
              layout="prev, pager, next"
              :total="totalCount">
            </el-pagination>
          </div>
        </div>
      </el-main>
    </el-container>


    <el-form :model="ICareWork" :rules="rules" ref="addEmpForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="InsdialogVisible"  width="65%">
          <el-row>
            <el-col :span="8">
              <el-form-item label="姓 名:" prop="name">
                <el-input prefix-icon="el-icon-edit" v-model="ICareWork.name"  size="mini" style="width: 200px"
                          placeholder="请输入姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="性   别:" prop="sex">
                <el-select  v-model="ICareWork.sex" size="mini"  placeholder="请选择性别" style="width:200px">
                  <el-option  v-for="item in sexData" :key="item.sex" :label="item.mame" :value="item.sex"> </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="身 份 证:" prop="idcard">
                <el-input prefix-icon="el-icon-edit" v-model="ICareWork.idcard"  size="mini" style="width: 210px"
                          placeholder="请输入身份证"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="职 务:" prop="linkman">
                <el-input prefix-icon="el-icon-edit"  disabled="disabled"  size="mini" style="width: 210px"
                          placeholder="护工"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="联系方式:" prop="phone">
                <el-input prefix-icon="el-icon-edit" v-model="ICareWork.phone"  size="mini" style="width: 170px"
                          placeholder="请输入联系方式"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="16">
              <el-form-item label="联系地址:" prop="address">
                <el-input prefix-icon="el-icon-edit" v-model="ICareWork.address"  size="mini" style="width: 350px"
                          placeholder="请输入联系地址"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="addEmp('addEmpForm')">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>


    <el-form :model="CareWork" :rules="rules" ref="addEmpForm" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="dialogVisible"  width="65%">
          <el-row>
            <el-col :span="8">
              <el-form-item label="员工编号:" prop="code">
                <el-input prefix-icon="el-icon-edit" v-model="CareWork.code" disabled="disabled"  size="mini" style="width: 200px"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="姓 名:" prop="name">
                <el-input prefix-icon="el-icon-edit" v-model="CareWork.name"  size="mini" style="width: 210px"
                          placeholder="请输入姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="性   别:" prop="sex">
                <el-select  v-model="CareWork.sex" size="mini"  placeholder="请选择性别" style="width:200px">
                  <el-option  v-for="item in sexData" :key="item.sex" :label="item.mame" :value="item.sex"> </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="身 份 证:" prop="idcard">
                <el-input prefix-icon="el-icon-edit" v-model="CareWork.idcard"  size="mini" style="width: 210px"
                          placeholder="请输入身份证"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="职 务:" prop="linkman">
                <el-input prefix-icon="el-icon-edit"  disabled="disabled" size="mini" style="width: 210px"
                          placeholder="护工"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="联系方式:" prop="phone">
                <el-input prefix-icon="el-icon-edit" v-model="CareWork.phone"  size="mini" style="width: 170px"
                          placeholder="请输入联系方式"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="16">
              <el-form-item label="联系地址:" prop="address">
                <el-input prefix-icon="el-icon-edit" v-model="CareWork.address"  size="mini" style="width: 350px"
                          placeholder="请输入联系地址"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="modifyClick()">确 定</el-button>
               </span>
        </el-dialog>
      </div>
    </el-form>

    <el-form v-model="behaivor" :rules="rules" style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="transaVisible"  width="65%">
          <el-row>
            <el-col :span="8">
              <el-form-item label="服务单据号:" prop="recordCode">
                <el-input prefix-icon="el-icon-edit" v-model="behaivor.recordCode"  size="mini" style="width: 200px"
                          placeholder="请输入服务单据号"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="记录日期:">
                <div class="block">
                  <el-date-picker v-model="behaivor.datetime"  value-format="yyyy-MM-dd hh:mm:ss" type="datetime"
                                  style="width: 175px" size="mini" placeholder="选择日期">
                  </el-date-picker>
                </div>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="评价等级:">
                 <el-select  v-model="behaivor.grade" size="mini"  placeholder="请选择评价等级" style="width:150px">
                    <el-option  v-for="item in gradeData" :key="item.grade" :label="item.mame" :value="item.grade"> </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-form-item label="事件:" style="float: left">
              <el-input type="textarea" v-model="behaivor.event" style="width: 800px"></el-input>
            </el-form-item>
          </el-row>
          <span slot="footer" class="dialog-footer">
               <el-button size="mini" @click="cancelEidt">取 消</el-button>
              <el-button size="mini" type="primary" @click="addBehaivor">添 加</el-button>
          </span>
        </el-dialog>
      </div>
    </el-form>

    <el-form style="margin: 0px;padding: 0px;">
      <div style="text-align: left">
        <el-dialog  :title="dialogTitle" style="padding: 0px;" :close-on-click-modal="false" :visible.sync="arrearageVisible"  width="65%">
          <!--  <el-row>
              <el-col :span="8">
                <el-form-item label="交易日期:">
                  <div class="block">
                    <el-date-picker
                      v-model="startDateAndEnd"
                      type="daterange"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期"
                      value-format="yyyy-MM-dd"
                      size="mini"
                      style="width:200px" >
                    </el-date-picker>
                  </div>
                </el-form-item>
              </el-col>
              <el-col :span="5">
                <el-form-item  >
                  <el-button size="mini"  type="primary" @click="getTransa">查询</el-button>
                </el-form-item>
              </el-col>
            </el-row>-->
          <el-row>
            <el-table
              :data="arrearageList"
              v-loading="tableLoading"
              border
              size="mini"
              style="width: 100%">
              <el-table-column
                prop="recordcode"
                align="center"
                label="服务请求单据号">
              </el-table-column>
              <el-table-column
                prop="code"
                align="center"
                label="护工编号">
              </el-table-column>
              <el-table-column
                prop="datetime"
                align="center"
                label="记录日期">
              </el-table-column>
              <el-table-column
                prop="grade"
                align="center"
                label="评价等级">
              </el-table-column>
              <el-table-column
                prop="event"
                align="center"
                label="事件">
              </el-table-column>
            </el-table>
            <div style="display: flex;justify-content: space-between;margin: 2px;float: right">
              <el-pagination
                background
                :page-size="10"
                :current-page="currentPage"
                @current-change="ArrChange"
                layout="prev, pager, next"
                :total="ArrCount">
              </el-pagination>
            </div>
          </el-row>
          <span slot="footer" class="dialog-footer">
              <el-button size="mini" type="primary" @click="cancelEidt()">关 闭</el-button>
          </span>
        </el-dialog>
      </div>
    </el-form>


  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        patientList:[],
        transactionList:[],
        arrearageList:[],
        roleName:'',

        dialogTitle: '',
        resourceSelection:[],
        startDateAndEnd:'',
        totalCount: -1,
        ArrCount:-1,
        currentPage:1,
        dialogVisible: false,
        tableLoading: false,
        InsdialogVisible:false,
        arrearageVisible:false,
        transaVisible:false,
        advanceSearchViewVisible: false,

        CareWork: {
          code:null,
          name: '',
          idcard: '',
          sex: '1',
          phone: '',
          address: '',
          jodtitle:'1',
        },
        ICareWork: {
          name: '',
          idcard: '',
          sex: '',
          phone: '',
          address: '',
          jodtitle:'1',
        },

        behaivor:{
          grade:'',
          recordCode:'',
          datetime:'',
          event:'',
          id:'',
        },

        patientResource: {
          name: '',
          id:''
        },
        rules: {
          name: [{required: true, message: '必填:姓名', trigger: 'blur'}],
          // idCard: [{required: true, message: '必填:身份 证', trigger: 'blur'}],
          //   sex: [{required: true, message: '必填:性别', trigger: 'blur'}],
          phone: [{required: true, message: '必填:联系方式', trigger: 'blur'}],
          //  address: [{required: true, message: '必填:联系地址', trigger: 'blur'}],
          // linkman: [{required: true, message: '必填:家属信息', trigger: 'blur'}],
          //recordCode: [{required: true, message: '必填:服务请求单编号', trigger: 'blur'}],

        },
        sexData:[
          {
            sex:'1',
            mame:'男'
          },{
            sex:'2',
            mame:'女'
          }
        ],
        gradeData:[
          {
            grade:'0',
            mame:'优'
          },{
            grade:'1',
            mame:'良'
          },{
            grade:'2',
            mame:'差'
          }
          ],

      };
    },
    mounted: function () {
      this.loadEmps();
    },
    methods: {
      keywordsChange(val){
        if (val == '') {
          this.loadEmps();
        }
      },
      searchEmp(){
        this.loadEmps();
      },
      //员工信息分页
      currentChange(currentChange){
        this.currentPage = currentChange;
        this.loadEmps();
      },
      //欠费分页
      ArrChange(ArrChange){
        this.currentPage = ArrChange;
        this.getArrearage();
      },
      loadEmps(){
        const _this = this;
        this.tableLoading = true;
        this.postRequestJson('/baseinfo/employee/list',{
          name: this.roleName,
          curPage:this.currentPage,
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if(resp.data.code === '200'){
              let data=[];
              let typeNmae="";
              for(let i=0;i<resp.data.msg.result.length;i++){
                if(resp.data.msg.result[i].jodtitle=1){
                  typeNmae="护工";
                }
                data.push({
                  code:resp.data.msg.result[i].code,
                  name:resp.data.msg.result[i].name,
                  phone:resp.data.msg.result[i].phone,
                  idcard:resp.data.msg.result[i].idcard,
                  address:resp.data.msg.result[i].address,
                  sex:resp.data.msg.result[i].sex==1?"男":"女",
                  id:resp.data.msg.result[i].id,
                  jodtitle:typeNmae
                });
              }
              _this.patientList =data;
              _this.totalCount = resp.data.msg.totalSize;
            }else{
              this.$message.error({
                type:'error',
                message:resp.data.msg
              });
            }
          }else{
            this.$message.error({
              type:'error',
              message:resp.status
            });
          }
        })
      },
      //添加病人基础信息
      addEmp(formName){
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //添加
            this.tableLoading = true;
            this.postRequestJson("/baseinfo/employee",this.ICareWork).then(resp=> {
              _this.tableLoading = false;
              if (resp && resp.status === 200) {
                _this.dialogVisible = false;
                if(resp.data.code=200){
                  this.getMsg("添加成功");
                }else{
                  this.getMsg(resp.data.msg);
                }
                this.InsdialogVisible = false;
              }else{
                this.getMsg(resp.status);
              }
            })
          } else {
            return false;
          }
        });
      },
      //修改
      modifyClick(){
        var _this = this;
        this.tableLoading = true;
        //修改
        this.putRequestJson("/baseinfo/employee",this.CareWork).then(resp=> {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            this.getMsg(resp.data.msg);
            this.dialogVisible = false;
          }else{
            this.getMsg(resp.status);
          }
        })
      },
      //取消按钮
      cancelEidt(){
        this.dialogVisible = false;
        this.InsdialogVisible=false;
        this.transaVisible=false;
        this.arrearageVisible=false;
      },
      //编辑护工基础信息对话框
      showEditEmpView(row){
        this.dialogTitle = "编辑护工基础信息";
        this.dialogVisible = true;
        this.modifyEmployee(row.code)
      },
      //添加行为交易记录对话框
      transaView(row){
        this.dialogTitle = "添加行为记录";
        this.transaVisible=true;
        this.behaivor.id=row.id;
      },

      //添加行为记录
      addBehaivor(){
        this.postRequestJson('/baseinfo/employee/behaivor',{
          datetime: this.behaivor.datetime,
          event: this.behaivor.event,
          recordcode: this.behaivor.recordCode,
          grade:this.behaivor.grade,
          projectid:2,
          employeeid:this.behaivor.id
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            if(resp.data.code="200"){
              this.getMsg(resp.data.msg);
              this.transaVisible=false;
            }else{
              this.getMsg(resp.data.msg);
            }
          }else{
            this.getMsg(resp.status);
          }
        })
      },

      //行为记录表
      arrearageView(row){
        this.dialogTitle = "行为记录表";
        this.arrearageVisible=true;
        this.getArrearage(row.code);
      },
      //查询行为记录
      getArrearage(code){
        this.arrearageList=[];
        this.tableLoading = true;
        this.postRequestJson('/baseinfo/employee/behavior/list',{
          code: code,
          curPage:this.currentPage,
          pageSize:10
        }).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status === 200) {
            let data=[];
            let grdeMnaem='';
            if(resp.data.code===200 && resp.data.msg.result){
              if(resp.data.msg.result.length>0){
                for(let i=0;i< resp.data.msg.result.length;i++){
                  if(resp.data.msg.result[i].grade=0){
                    grdeMnaem="优"
                  }else  if(resp.data.msg.result[i].grade=1){
                    grdeMnaem="良"
                  }else  if(resp.data.msg.result[i].grade=2){
                    grdeMnaem="差"
                  }
                  data.push({
                    code:resp.data.msg.result[i].code,
                    datetime:resp.data.msg.result[i].datetime,
                    event:resp.data.msg.result[i].event,
                    recordcode:resp.data.msg.result[i].recordcode,
                    grade:grdeMnaem
                  })
                }
              }else {
                this.getMsg("暂无数据");
              }
            }else{
              this.getMsg(resp.data.msg);
            }
            this.arrearageList =data;
            this.ArrCount = resp.data.msg.totalSize;
          }else{
            this.getMsg(resp.status);
          }
        })
      },

      //查询护工详细信息
      modifyEmployee(code){
        this.getRequest("/baseinfo/employee?code="+code).then(resp=> {
          if (resp && resp.status === 200) {
            if(resp.data.code=200){
              this.CareWork.code=resp.data.msg.code;
              this.CareWork.name=resp.data.msg.name;
              this.CareWork.idcard=resp.data.msg.idcard;
              this.CareWork.address=resp.data.msg.address;
              this.CareWork.phone=resp.data.msg.phone;
            }else{
              this.getMsg(resp.data.msg);
            }
          }else{
            this.getMsg(resp.data.msg);
          }
        })
      },

      //添加护工基础对话框
      showAddEmpView(){
        this.dialogTitle = "添加护工基础信息";
        this.InsdialogVisible = true;
        this.ICareWork.address='';
        this.ICareWork.idcard='';
        this.ICareWork.jodtitle='';
        this.ICareWork.name='';
        this.ICareWork.phone='';
        this.ICareWork.sex='';
      },
      getMsg(data){
        this.$confirm( data, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.loadEmps();
        }).catch(() => {
        });
      },
    }
  };
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



